.main-wrapper { padding: 0 0 0 0; min-width: 320px; width: 100%; position: relative; overflow: hidden; min-height: 100%; display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; flex-direction: column; -webkit-flex-direction: column; -ms-flex-direction: column; justify-content: flex-start; -webkit-justify-content: flex-start; -ms-flex-pack: start; }

.wrapper { min-width: 320px; max-width: 1200px; padding: 0 40px 0 40px; margin: 0 auto; position: relative; }

/* titles */
p { font-size: 13px; line-height: 18px; color: #282828; padding: 15px 0 15px 0; font-weight: 400; }

h1 { font-size: 36px; line-height: 42px; color: #2b2b2b; font-family: "Open Sans", sans-serif; padding: 15px 0 15px 0; font-weight: 400; }

h2 { font-size: 32px; line-height: 37px; color: #2b2b2b; font-family: "Open Sans", sans-serif; padding: 15px 0 15px 0; font-weight: 400; }

h3 { font-size: 28px; line-height: 32px; color: #2b2b2b; font-family: "Open Sans", sans-serif; padding: 15px 0 15px 0; font-weight: 400; }

/* text position */
.text-left { text-align: left !important; }

.text-center { text-align: center !important; }

.text-right { text-align: right !important; }

.nowrap { white-space: nowrap !important; }

/* loader */
.loaded .main-wrapper { visibility: hidden; }

.icon-load { background: url(../img/loader.gif) no-repeat left top; width: 40px; height: 40px; position: fixed; left: 50%; top: 50%; margin-left: -20px; margin-left: -20px; display: none; }

.loaded .icon-load { display: block; }

/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.content { min-width: 320px; text-align: left; width: 100%; order: 2; -webkit-order: 2; flex-grow: 1; -webkit-flex-grow: 1; background: url("../../img/decor/main_bg.png") repeat; padding-bottom: 120px; }

.title { padding: 2.5rem 0; font-family: 'Comfortaa', cursive; font-weight: bold; color: #2b2b2b; font-size: 1.875rem; letter-spacing: -2px; }

/*PRODUCTS*/
.main__item { min-height: 380px; margin-bottom: 30px; position: relative; background: #ffffff; font-family: "Open Sans", sans-serif; color: #282828; font-size: 15px; border-radius: 4px; overflow: hidden; box-shadow: 0px 1px 2.8px 1.2px rgba(0, 0, 0, 0.09); transition: all .3s; }

.main__item:hover { box-shadow: 0px 0px 1px 1px #c2e8f0, 0px 5px 10.56px 1.44px rgba(68, 190, 217, 0.36); }

.main__item:hover .item__title { color: #23a2be; }

.item__img img { width: 100%; }

.item__desc { text-align: center; padding: 1.25rem 0.3125rem; }

.item__rating { margin-bottom: 0.9375rem; }

.rating__star { background-image: url("../../img/icons/star.svg"); height: 16px; width: 16px; display: inline-block; }

.rating__star.active { background-image: url("../../img/icons/star-active.svg"); }

.item__title { color: #282828; margin-bottom: 1.25rem; }

.item__price { display: inline-block; font-weight: bold; padding-top: 1.25rem; position: relative; }

.item__price:after { content: ''; display: block; position: absolute; top: 0px; left: -15%; width: 130%; height: 1px; background: #dbdbdb; }

.main__abs { position: absolute; top: 11px; left: 11px; font-size: 0.6875rem; font-weight: 500; text-transform: uppercase; display: inline-block; padding: 0.625rem 0.375rem; border-radius: 2px; color: #fff; }

.main__abs_new { background: #48b3cb; }

.main__abs_hit { background: #ffae2e; }

.similar__title { margin-top: 15px; margin-bottom: 20px; font-size: 1rem; font-family: "Open Sans", sans-serif; color: #282828; text-transform: uppercase; }

/*END PRODUCTS*/
/*FORM*/
.filter__button__wrap { text-align: center; }

.filter__button__wrap .filter_btn { display: none; padding: 10px 15px; font-size: 14px; background: #f1f1f1; margin-bottom: 15px; cursor: pointer; }

.filter_form { border: 1px solid #e1e1e1; background: #fbfbfb; margin-bottom: 15px; }

.filter_form input[type='radio'] { position: absolute; left: -99999px; }

.filter__form__content__drop { padding: 0px 15px 0 30px; max-height: 180px; height: 0; overflow-x: hidden; overflow-y: auto; transition: all .3s; }

.filter__form__content__drop label { display: block; color: #444444; font-size: 13px; font-weight: 400; padding: 7.5px 0; transition: all .2s ease-out; opacity: 0; transform: translateY(-5px); }

.filter__form__content__drop label:nth-child(1) { transition-delay: opacity .07s, transform .07s; }

.filter__form__content__drop label:nth-child(3) { transition-delay: opacity .14s, transform .14s; }

.filter__form__content__drop label:nth-child(5) { transition-delay: opacity .21s, transform .21s; }

.filter__form__content__drop label:nth-child(7) { transition-delay: opacity .28s, transform .28s; }

.filter__form__content__drop label:hover { font-weight: 500; color: #39a6be; cursor: pointer; }

.filter__form__content__drop label.active { color: #39a6be; }

.open .filter__form__content__drop { height: 180px; border-bottom: 1px solid #e1e1e1; padding: 7.5px 15px 0 30px; }

.open .filter__form__content__drop label { opacity: 1; transform: translateY(0px); }

.open .filter__form__link_drop:before { transform: rotate(45deg); }

.open .filter__form__link_drop:after { transform: rotate(-45deg); }

.filter_form__size-wrap, .filter_form__color-wrap { border-bottom: 1px solid #e1e1e1; padding: 5px 5px 5px 20px; }

.filter_form__size-wrap label, .filter_form__color-wrap label { width: 30%; }

.filter_form__size-wrap input, .filter_form__size-wrap select, .filter_form__color-wrap input, .filter_form__color-wrap select { font-family: "Open Sans", sans-serif; font-size: 14px; padding: 8px 10px; width: 67%; background: #f3f3f3; outline: none; border: 1px solid #fff; border-radius: 3px; box-shadow: inset 0px 1px 3px 0px rgba(0, 0, 0, 0.14); }

#color:focus { border: 1px solid #39a6be; }

::-webkit-input-placeholder { color: #464646; }

::-moz-placeholder { color: #464646; }

/* Firefox 19+ */
:-moz-placeholder { color: #464646; }

/* Firefox 18- */
:-ms-input-placeholder { color: #464646; }

.filter_form__label { display: inline-block; }

.filter__form__link_drop { border-bottom: 1px solid #e1e1e1; text-decoration: none; display: block; padding: 15px 20px; position: relative; }

.filter__form__link_drop:before { content: ''; position: absolute; width: 7px; height: 1px; right: 16px; top: 50%; background: #939393; transition: all .3s; transform: rotate(-45deg); }

.filter__form__link_drop:after { content: ''; position: absolute; width: 7px; height: 1px; right: 21px; top: 50%; background: #939393; transition: all .3s; transform: rotate(45deg); }

.filter_form__label, .filter__form__link_drop { font-size: 14px; color: #343434; font-weight: bold; }

.filter_form__btn { display: block; width: 100%; background: transparent; border: none; padding: 0; text-align: center; padding: 15px 0; transition: all .3s; }

.filter_form__btn:hover { color: #39a6be; }

/*END FORM*/
