.slick-arrow { position: absolute; height: 38px; width: 38px; border-radius: 50%; border: none; background: #f1f1f1; }

.slick-prev { top: calc(0% - 17px); left: -60px; }

.slick-prev:after { content: ''; position: absolute; width: 10px; height: 2px; background: #51b5ca; top: 42%; left: 37%; transform: rotate(-45deg); }

